<template>
  <div class="content-tpl seven-tpl">
    <h4></h4>
    <div class="base-info">
      <p> <span class="s-value">{{tplData.partya}}</span></p>
      <p> <span class="s-value">{{tplData.partyb}}</span></p>
      <p></p>
    </div>
    <div class="terms">
      <div v-for="item in itemData" :key="item.val">
        {{item.val}}
        <p v-if="item.sub" v-for="sub in item.sub" :key="sub">
          {{sub}}
        </p>
      </div>
    </div>
    <div class="sign-info">
      <p>()</p>
      <p>()</p>
      <p></p>
      <p></p>
      <p> <label>{{tplData.date}}</label></p>
      <p> <label>{{tplData.date}}</label></p>
    </div>
  </div>
</template>
<script>
  export default {
    props:['tplData'],
    data() {
      return {
        itemData:[
          {'val':'1'},
          {'val':'2'},
          {
            'val':'3',
            'sub':[
              '1) ',
              '2) 9:0017:00',
              '3) ',
              '4) ',
              '5) 4872',
              '6) '
            ]
          },
          {'val':'4'},
          {'val':'5'},
          {'val':'6POP'},
          {'val':'7'},
          {
            'val':'',
            'sub':[
              ' 30',
              ' ',
              ' ',
              ' ',
              ' ',
              ' ',
              ' , '
            ]
          },
          {
            'val':'',
            'sub':[
              '1',
              '2',
              '3',
              '4',
              '5',
              '6POS',
              '7',
              '8,5000/,',
              '9',
              '10'
            ]
          }
        ]
      }
    },
    methods: {

    }
  };
</script>
<style lang="less" scoped>
  .seven-tpl{
    .base-info{
      p{
        width: 100% !important;
      }
      p:last-child{
        text-indent: 2em;
      }
    }
  }
</style>



