<template>
  <div class="content-tpl four-tpl">
    <h4></h4>
    <div class="terms">
      <div v-for="item in itemData" :key="item">
          {{item}}
      </div>
    </div>
    <div class="sign-info">
      <p>()</p>
      <p>()</p>
      <p>/</p>
      <p>/</p>
      <p> <label>{{tplData.date}}</label></p>
    </div>
  </div>
</template>
<script>
  export default {
    props:['tplData'],
    data() {
      return {
        itemData:[
          '',
          '1',
          '2',
          '3',
          '4',
          '5',
          '6',
          '7',
          '8',
          '9304'
        ],
      }
    },
  };
</script>
<style lang="less" scoped>
  .four-tpl{
    .terms{
      &>div{
        text-indent: 2em;
      }
    }
  }
</style>



