import pagenation from '@/components/pagenation'
import { mapGetters } from 'vuex'
export const pageListMixin = { //list
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      itemList:[],
      pageInfo:{},
      getListUrl:''
    }
  },
  methods: {
    async getList(url) {
      let data = {
        ...this.reqData
      };
      this.getListUrl = url;
      if(this.beginTime && this.beginTime.length){ // -
        data.beginRentBeginTime = this.beginTime[0];
        data.endRentBeginTime = this.beginTime[1]
      }
      if(this.endTime && this.endTime.length){  // -
        data.beginRentEndTime = this.endTime[0];
        data.endRentEndTime = this.endTime[1]
      }
      if(this.paymentTime && this.paymentTime.length){ //-
        data.startTime =  this.paymentTime[0];
        data.endTime =  this.paymentTime[1];
      }
      if(this.refundTime && this.refundTime.length){ //-
        data.refundApplyBeginTime =  this.refundTime[0];
        data.refundApplyEndTime =  this.refundTime[1];
      }
      let res = await this.$http.get(url,{params: data});
      if(res){
        this.itemList = res.data;
        this.pageInfo = res.page;
      }
    },
    indexMethod(index) {
      let currentPage = this.pageInfo && this.pageInfo.currentPage || 1;
      return index + (currentPage - 1)*20 + 1
    },
    handlePage(page){
      this.reqData.currentPage = page || 1;
      this.getList(this.getListUrl);
    }
  },
  components:{
    pagenation
  },
  computed: {
    ...mapGetters({
      checkKey: 'getPermission',
    })
  }
};

export const contractDetailMixin = { //
  data() {
    return {
      itemListInfo:[],
      itemList:[],
      merchant:{}, //
    }
  },
  computed:{
    canInput() {
      return this.submitData.costId.length === 1
    }
  },
  methods: {
    async getData(url, contractBatchNum, deposit = '') {
      let curUrl;
      if(deposit){
        curUrl = `${url}/${contractBatchNum}?deposit=${deposit}`
      }else {
        curUrl = `${url}/${contractBatchNum}`
      }
      let res = await this.$http.get(curUrl);
      if(res){
        this.merchant = res.data.merchant;
        this.itemList = res.data.list || res.data.resultRefundDTO;
        this.paymentSuccess = res.data.paymentSuccess;
        this.unpaidPayment = res.data.unpaidPayment;
      }
    },
    handleSelectionChange(val) {
      this.submitData.costId = [];
      this.submitData.payAmount =  0;
      val.forEach((item)=>{
        this.submitData.costId.push(item.contractCostId);
        this.submitData.payAmount += item.unpaidPayment || 0;
      });
      if(this.submitData.payAmount > 0){
        this.submitData.payAmount = this.submitData.payAmount.toString();
      }
      if(this.submitData.costId.length > 1){
        this.$refs['submitData'].clearValidate(['payAmount']);
      }
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '';
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          sums[index] += '';
        } else {
          sums[index] = '--';
        }
      });
      return sums;
    }
  }
};

// ajax
export const ajaxData = {
  methods:{
    /**
     * ajax ajaxGet,ajaxPost,ajaxPut,ajaxDelete
     * @param bean
     * url,data,
     * resetForm model,
     * validateModel model,
     * callBack ajax,
     * success bool false ,
     * fail bool false 
     * validateAfterBool 
     * validateAfter ,if false,true
     */
    ajaxRequest(bean){
      // 
      if(bean.validateModel){
        this.$refs[bean.validateModel].validate((valid) => {
          if(valid){
            if(bean.validateAfterBool){
              if(bean.validateAfter()){
                this.ajaxDetail(bean);
              }else{
                bean.validateAfter();
              }
            }else{
              this.ajaxDetail(bean);
            }
          }else{
            return false
          }
        });
      }else{
        this.ajaxDetail(bean);
      }
    },
    ajaxDetail(bean){
      this.$http({
        url:bean.url,
        method:bean.method,
        data:bean.data,
        params:bean.data
      }).then(res => {
        if(res){
          if(res.code == 0){
            if(bean.success){
              this.$message.success(res.msg)
            }
            if(bean.resetForm){
              this.$refs[bean.resetForm].resetFields();
            }
            if(bean.callBack){
              bean.callBack(res);
            }
          }else{
            if(bean.fail){
              this.$message.error(res.msg)
            }
          }
        }
      });
    },
    ajaxGet(bean){
      bean.method = 'get';
      bean.data = {
        params: bean.data
      }
      this.ajaxRequest({...bean});
    },
    ajaxPost(bean){
      bean.method = 'post';
      this.ajaxRequest({...bean});
    },
    ajaxPut(bean){
      bean.method = 'put';
      this.ajaxRequest({...bean});
    },
    ajaxDelete(bean){
      bean.method = 'delete';
      this.ajaxRequest({...bean});
    }
  }
};

// 
export const messageBox = {
  methods: {
    /**
     * confirm 
     * @param bean
     * message, title, btn(,), type, callBack, cancelBack
     */
    confirmBox(bean){
      this.$confirm(
        bean.message,
        bean.title ? bean.title : '',
        {
          confirmButtonText: bean.btn && bean.btn.split(',').length > 1 ? bean.btn.split(',')[0] : '',
          cancelButtonText: bean.btn && bean.btn.split(',').length > 1 ? bean.btn.split(',')[1] : '',
          type: bean.type? bean.type :'warning'
        }
      ).then(() => {
        if(bean.callBack){
          bean.callBack();
        }
      }).catch(() => {
        if(bean.cancelBack){
          bean.cancelBack();
        }
      });
    }
  }
};



