<template>
  <div class="content-tpl eight-tpl">
    <h4></h4>
    <div class="base-info">
      <p> <span class="s-value">{{tplData.partya}}</span></p>
      <p> <span class="s-value">{{tplData.partyb}}</span></p>
      <p></p>
    </div>
    <div class="terms">
      <div v-for="item in itemData" :key="item.val">
        {{item.val}}
        <p v-if="item.sub" v-for="sub in item.sub" :key="sub">
          {{sub}}
        </p>
      </div>
    </div>
    <div class="sign-info">
      <p>()</p>
      <p>()</p>
      <p></p>
      <p></p>
      <p> <label>{{tplData.date}}</label></p>
      <p> <label>{{tplData.date}}</label></p>
    </div>
  </div>
</template>
<script>
  export default {
    props:['tplData'],
    data() {
      return {
        itemData:[
          {'val':'1'},
          {'val':'2'},
          {'val':'3'},
          {'val':'4'},
          {'val':'5'},
          {'val':'6'},
          {'val':'7'},
          {'val':'8'},
          {
            'val':'9',
            'sub':[
              ' 9:206:00',
              ' ',
              ' 9:25',
              ' ',
              ' ',
              ' ',
              ' ',
              ' ',
            ]
          },
          {
            'val':'',
            'sub':[
              ' 10-50',
              ' 3,,100-200',
              ' 3, '
            ]
          }
        ]
      }
    },
    methods: {

    }
  };
</script>
<style lang="less" scoped>
  .eight-tpl{
    .base-info{
      p{
        width: 100% !important;
      }
      p:last-child{
        text-indent: 2em;
      }
    }
  }
</style>



