<template>
  <div class="content-tpl three-tpl">
    <h4></h4>
    <div class="base-info">
      <p> <span class="s-value">{{tplData.partya}}</span></p>
      <p> <span class="s-value">{{tplData.partyb}}</span></p>
      <p></p>
    </div>
    <div class="terms">
      <h5><span></span></h5>
      <div v-for="item in receiptA" :key="item">
          {{item}}
      </div>
      <div>
        6
        <p><label>{{tplData.refundAccountBank}}</label></p>
        <p>  <label>{{tplData.refundAccountName}}</label></p>
        <p><label>{{tplData.refundAccountNumber}}</label></p>
