      <div>2</div>
      <div>33</div>
      <h5><span></span></h5>
      <div>
        <p></p>
        <p></p>
        <p>POS,,POS.,</p>
      </div>
      <h5><span></span></h5>
    </div>
    <div class="sign-info">
      <p>()</p>
      <p>()</p>
      <p></p>
      <p></p>
      <p> <label>{{tplData.date}}</label></p>
      <p> <label>{{tplData.date}}</label></p>
    </div>
  </div>
</template>
<script>
  export default {
    props:['tplData'],
    data() {
      return {
        receiptA:[
          '1POS',
          '2<<>>,',
          '3<<>><<>><<>>POSPOSPOSPOSPOSPOS',
          '4POSPOSPOS',
          '5()10',
        ],
        receiptB:[
          '7<<>>3()',
          '8',
          '9',
          '10',
          '11',
          '1210()'
        ]
      }
    }
  };
</script>
<style lang="less" scoped>
  .three-tpl{
    .base-info{
      p{
        width: 100% !important;
      }
      p:last-child{
        text-indent: 2em;
      }
    }
  }
</style>



